@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&family=Poppins&family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed&display=swap');

$HEADER_TOP_BG: #FFFFFF;
$PRIMARY_COLOR: #131921;
$SECONDARY_COLOR: #222F3E;

$PRIMARY_FONT: "Poppins", sans-serif;
$SECONDARY_FONT: "Roboto", sans-serif;
$THIRD_FONT: "Roboto Condensed", sans-serif;

$PRIMARY_HEADER_COLOR: #FFFFFF;

$PRIMARY_THEME_COLOR: #FFA41C;
$SECONDARY_THEME_COLOR: #1995D3;
$THIRD_THEME_COLOR: #1AB744;
$PRIMARY_BG_COLOR: #F7F7F7;
$TRANSITION: all ease-in-out 0.3s;

.main-place-order-container{
    width: 100%;
    background-color: $PRIMARY_BG_COLOR;
    display: flex;
    justify-content: space-evenly;
    padding: 0.5rem;
    flex-wrap: wrap;
}

.place-order-container{
    background-color: white;
    width: 100%;
    display: flex;
    gap: 0.25rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0.25rem;
    border-radius: 5px;
}

.place-order-card-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0.25rem;
    width: 100%;
    gap: 0.5rem;
}

.order-card{
    background-color: $HEADER_TOP_BG;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 300px;
    border-radius: 5px;
    height: fit-content;
    border: 1px solid #EBEBEB;
}


.order-card-title{
    font-size: 15px;
    font-family: $PRIMARY_FONT;
    color: $PRIMARY_COLOR;
    padding: 10px 0;
    font-weight: 600;
    border-bottom: 1px solid #EBEBEB;
}

.order-card > span {
    font-size: 14px;
    font-family: $SECONDARY_FONT;
    color: $PRIMARY_COLOR;
}

.place-order-items{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 10px;
}

.place-order-item-span{
    font-size: 14px;
    font-family: $PRIMARY_FONT;
}

.placer-order-btn{
    background-color: $SECONDARY_THEME_COLOR;
    border-radius: 5px;
    color: white;
    font-size: 16px;
    font-family: $SECONDARY_FONT;
    text-transform: uppercase;
}

.order-id{
    font-size: 16px;
    background-color: #B0DAAD;
    color: white;
    padding: 5px 10px;
    border-radius: 5px;
    font-family: $PRIMARY_FONT;
}

@media screen and (max-width: 576px) {
    .main-place-order-container{
        justify-content: start;
        padding: 0.25rem;
    }

    .place-order-card-container{
        flex-direction: column;
    }
    .order-card{
        width: 100%;
    }

    .order-card-title{
        font-size: 14px;
    }

    .order-card > span, .place-order-item-span {
        font-size: 12px;
    }

    .place-order-items{
        flex-direction: column;
    }

    .placer-order-btn{
        font-size: 13px;
    }

    .order-id{
        font-size: 13px;
        padding: 5px 5px;
    }
    
}