@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&family=Poppins&family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed&display=swap');

$HEADER_TOP_BG: #FFFFFF;
$PRIMARY_COLOR: #131921;
$SECONDARY_COLOR: #222F3E;

$PRIMARY_FONT: "Poppins", sans-serif;
$SECONDARY_FONT: "Roboto", sans-serif;
$THIRD_FONT: "Roboto Condensed", sans-serif;

$PRIMARY_HEADER_COLOR: #FFFFFF;

$PRIMARY_THEME_COLOR: #FFA41C;
$SECONDARY_THEME_COLOR: #1995D3;
$THIRD_THEME_COLOR: #1AB744;
$PRIMARY_BG_COLOR: #F7F7F7;
$TRANSITION: all ease-in-out 0.3s;

$SHADOW: 0 1px 2px 0 rgba(0, 0, 0, 0.1), 0 2px 10px 0 rgba(0, 0, 0, 0.09);

.site-logo{
    text-decoration: none;
    font-size: 32px;
    font-weight: 800;
    color: white;
}


.site-logo:hover{
    text-decoration: none;
    color: white;
}


.header-links{
    color: white;
    font-size: 14px;
    font-family: $PRIMARY_FONT;
    transition: $TRANSITION;
    border: 2px solid white;
    padding: 5px 10px;
    border-radius: 5px;
}

.header-links:hover{
    transition: $TRANSITION;
    color: white;
    text-decoration: none;
}

.menu-arrow{
    color: lightgray;
    font-size: 18px;
}

.top-header-container{
    background-color: $PRIMARY_COLOR;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    position: sticky;
    top: 0;
    padding: 0.25rem 1rem;
}

.logo-container{
    display: flex;
    align-items: center;
    gap: 0.25rem;
}


.search-bar-container{
    display: flex;
    align-items: center;
    padding: 5px;
}

.main-search-bar{
    width: 550px;
    cursor: pointer;
    border: none;
    border-radius: 5px 0 0 5px;
    background-color:white;
    padding: 7px;
    font-size: 15px;
    font-family: $PRIMARY_FONT;
    border: 1px solid #EBEBEB;
}

.main-search-bar:focus{
    outline: none;
}

.btn-search{
    padding: 8px;
    border: none;
    background-color: $PRIMARY_THEME_COLOR;
    color: $PRIMARY_COLOR;
    border-radius: 0 5px 5px 0;
}

.btn-search-icon{
    font-size: 1.25rem;
    color: $PRIMARY_COLOR;
}

.signin-cart-container{
    display: flex;
    color: white;
    align-items: center;
    gap: 1rem;
    font-family: $PRIMARY_FONT;
}


.search-icon{
    display: none;
    font-size: 2rem;
    padding: 4px;
    border-radius: 5px;
    cursor: pointer;
    color: white;
}

.search-icon:hover{
    background-color: $SECONDARY_COLOR;
    color: white;
}

.shop-cart-icon{
    color: $PRIMARY_HEADER_COLOR;
    font-size: 1.75rem;
}

.cart-details{
    color: $PRIMARY_THEME_COLOR;
    font-size: 1.25rem;
    font-weight: 600;
    font-family: $SECONDARY_FONT;
}
.cart-icon-info{
    display: flex;
    align-items: center;
    padding: 5px;
    gap: 5px;
    cursor: pointer;
}



.account-icon{
    font-size: 2.25rem;
    padding: 5px;
    border-radius: 5px;
    color: $PRIMARY_THEME_COLOR;
    transition: $TRANSITION;
}

 
.account-icon:hover{
    background-color: $SECONDARY_COLOR;
    color: white;
    transition: $TRANSITION;
}

.sign-links{
    font-family: $SECONDARY_FONT;
    padding: 5px 10px;
    font-size: 16px;
}

.sign-links:hover{
    background-color: $PRIMARY_COLOR;
    color: white;
    border-radius: 5px;
    text-decoration: none;
}

@media screen and (max-width:576px) {
    .site-logo{
        font-size: 20px;
    }

    .header-links{
        font-size: 11px;
    }

    .main-search-bar{
        padding: 5px;
    }

    .btn-search{
        padding: 5px;
    }

    .btn-search-icon{
        font-size: 1rem;
    }

    .close-icon{
        font-size: 1rem;
    }
    .top-header-container{
        padding: 0.25rem;
    }
    .cart-icon-info{
        margin-right: 0.5rem;
    }
    .cart-details{
        font-size: 14px;
    }
    
}


@media screen and (max-width: 960px){

    .search-bar-container{
        display: none;
    }

    .search-icon{
        display: block;
        font-size: 1.75rem;
    }

}

.second-header-container{
    background-color: $SECONDARY_COLOR;
    align-items: center;
    display: flex;
    padding: 8px;
    gap: 1rem;
    justify-content: start;
}

.menu-bar-container-bottom{
    display: flex;
    justify-content: start;
    color: rgb(161, 136, 136);
}

.menu-bar-container-top{
    display: none;
}
.menu-bar{
    font-size: 34px;
    color: white;
    cursor: pointer;
    padding: 5px;
    transition: $TRANSITION;
    border-radius: 5px;
}

.menu-bar:hover{
    border: 1px solid white;
    transition: $TRANSITION;
    color: white;
}

.top-nav-data-container{
    display: flex;
    flex-direction: row;
    
}

.navi-item{
    padding: 5px 8px;
    transition: $TRANSITION;
}
.navi-item:hover{
    text-decoration: none;
    transition: $TRANSITION;
    color: $SECONDARY_COLOR;
    background-color: $SECONDARY_THEME_COLOR;
    border-radius: 5px;
}

.navi-item:hover .nav-title{
    color: $PRIMARY_HEADER_COLOR;
    transition: $TRANSITION;
}

.nav-title{
    color: white;
    font-size: 13px; 
    transition: $TRANSITION;
    font-family: $PRIMARY_FONT;
}


@media screen and (max-width: 1100px) {
    .second-header-container{
        display: none;
    }
    .top-nav-data-container{
        display: none;
    }

    .menu-bar-container-bottom{
        display: none;
    }

    .menu-bar-container-top{
        display: block;
    }
    
}

/*****************Sidebar****************************/

.nav-menu{
        display: flex;
        flex-direction: column;
        width: 30%;
        height: 100vh;
        position: fixed;
        left: -100%;
        transition: all 0.3s ease-out;
}

.nav-menu.active{
    left: 0;
    transition: all 0.5s ease-out;
    background: #ffffff;
    z-index: 1;
    top: 0;
    height: 100vh;
    overflow-y: scroll;
}

.sidebar-profile-container{
    position: fixed;
    height: 2rem;
    padding: 10px;
    width: auto; 
    background-color: $PRIMARY_COLOR;
}

.nav-item{
    padding: 10px;
    list-style-type: none;
}

.nav-item:hover{
    background-color: #EAEDED;
    cursor: pointer;
}

/*****************************************************************************************/
.show-searchbar{
    display: none;
    position: static;
    top: 0;
    width: 100%;
    background-color: white;
    padding: 10px;
}

.show-searchbar.search-active{
    display: flex;
    align-items: center;
    gap: 1rem;
    justify-content: space-between;
}

.close-icon{
    font-size: 1.5rem;
    color: gray;
    cursor: pointer;
}

@media screen and (max-width: 800px) {
    .main-search-bar{
        width: 100%;
    }
}

/*************************************************************************************/
.show-cart{
    display: none;
    right: 0;
    background-color: $PRIMARY_HEADER_COLOR;
    width: 325px;
    position: absolute;
    margin-right: 0.5rem;
    padding: 1rem;
    border-radius: 5px;
    z-index: 2500;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.show-cart.cart-active{
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.view-cart-main-container{
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.view-cart-container{
    display: flex;
    flex-direction: column;
    gap: 10px;
    border-bottom: 1px solid $PRIMARY_BG_COLOR;
    padding: 1rem;
    height: 250px;
    overflow-y: scroll;
}

.view-cart-container::-webkit-scrollbar {
    background-color: transparent;
}

.view-cart-container::-webkit-scrollbar-thumb {
    border-radius: 5px;
    width: 5px;
    background-color: $PRIMARY_BG_COLOR;
    cursor: pointer;
}

.btn-view-cart{
    background-color: $THIRD_THEME_COLOR;
    padding: 8px;
    font-family: $SECONDARY_FONT;
    font-size: 14px;
    border-radius: 5px;
    color: $PRIMARY_HEADER_COLOR;
    box-shadow: $SHADOW;
}

.btn-checkout{
    background-color: $SECONDARY_THEME_COLOR;
    padding: 8px;
    font-family: $SECONDARY_FONT;
    font-size: 14px;
    color: $PRIMARY_HEADER_COLOR;
    box-shadow: $SHADOW;
    border-radius: 5px;
}

.view-cart-item{
    display: flex;
    justify-content: space-between;
    gap: 5px;
    align-items: center;
    cursor: pointer;
}

.view-cart-title{
    font-size: 13px;
    font-family: $PRIMARY_FONT;
    color: $PRIMARY_COLOR;
}

.view-cart-price{
    color: gray;
    font-size: 13px;
}

.remove-view-cart{
    font-size: 24px;
    color: lightgray;
    padding: 5px;
    transition: $TRANSITION;
}

.remove-view-cart:hover{
    color: $PRIMARY_COLOR;
    background-color: $PRIMARY_BG_COLOR;
    transition: $TRANSITION;
}


@media screen and (max-widtH: 576px) {
    .btn-view-cart,.btn-checkout{
        font-size: 12px;
        padding: 6px;
    }
    .show-cart{
        margin-right: 0;
        width: 100%;
        border-radius: 0;
    }
    
}