@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&family=Poppins&family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed&display=swap');

$HEADER_TOP_BG: #FFFFFF;
$PRIMARY_COLOR: #131921;
$SECONDARY_COLOR: #222F3E;

$PRIMARY_FONT: "Poppins", sans-serif;
$SECONDARY_FONT: "Roboto", sans-serif;
$THIRD_FONT: "Roboto Condensed", sans-serif;

$PRIMARY_HEADER_COLOR: #FFFFFF;

$PRIMARY_THEME_COLOR: #FFA41C;
$SECONDARY_THEME_COLOR: #1995D3;
$THIRD_THEME_COLOR: #1AB744;
$PRIMARY_BG_COLOR: #F7F7F7;
$TRANSITION: all ease-in-out 0.3s;

$SHADOW: 0 1px 2px 0 rgba(0, 0, 0, 0.1), 0 2px 10px 0 rgba(0, 0, 0, 0.09);

.main-home-screen-container{
    background-color: $PRIMARY_BG_COLOR;
    width: 100%;
    padding: 0.5rem;
}

.main-products-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    background-color: $PRIMARY_HEADER_COLOR;
    gap: 0.5rem;
    padding: 1rem;
    font-family: $PRIMARY_FONT;
    z-index: 1;
    border: 1px solid $PRIMARY_BG_COLOR;
}

.single-card{
    width: 14rem;
    display: flex;
    padding: 0.5rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.single-card:hover{
    text-decoration: none;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.product-image{
    width: 11rem;
    padding: 1rem;
}

.product-title{
    font-size: 14px;
    font-weight: 500;
    color: $SECONDARY_COLOR;
    width: 100%;
    font-family: $SECONDARY_FONT;
    background-color: $PRIMARY_BG_COLOR;
    padding: 10px;
    border-radius: 5px;
    letter-spacing: 0.5px;
}



.product-price{
    color: #b12704;
    font-family: $SECONDARY_FONT;
    font-size: 16px;
    font-weight: 500;
    
}

.sizes{
    font-size: 16px;
    font-weight: 600;
    color: $SECONDARY_COLOR;
}

.product-btn-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
}

.btn-view-product{
    transition: $TRANSITION;
    border: 2px solid $SECONDARY_THEME_COLOR;
    border-radius: 5px;
    padding: 5px;
    width: 75px;
    font-family: $PRIMARY_FONT;
    color: $SECONDARY_THEME_COLOR;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    box-shadow: $SHADOW;
}

.btn-view-product:hover{
    background-color: $SECONDARY_THEME_COLOR;
    color: $PRIMARY_HEADER_COLOR;
}

.product-btn-container{
    height: min-content;
    display: flex;
    justify-content: space-between;
    width: 100%;
    gap: 5px;
    align-items: center;
    gap: 0.5rem;
}


@media screen and (max-width: 576px) {
    .single-card{
        width: 9rem;
    }
    .product-image{
        width: 100%;
    }

    .product-title{
        font-size: 13px;
    }

    .product-price{
        font-size: 12px;
        width: 100%;
    }

    .btn-view-product{
        font-size: 12px;
        padding: 4px;
        width: auto;
    }
}


/******************************************************************************************/
.product-screen-main-container{
    width: 100%;
    background-color: $PRIMARY_BG_COLOR;
    padding: 0.5rem;
}

.product-screen-container{
    padding: 0.25rem;
    width: 100%;
    background-color: white;
    border-radius: 5px;
}

.product-details-container{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.product-screen-img{
    width: 350px;
}

.product-detail-title{
    font-size: 24px;
    font-family: $THIRD_FONT;
    font-weight: 700;
    text-transform: uppercase;
    color: $PRIMARY_COLOR;
    letter-spacing: 1px;
}

.product-detail-price{
    color: #b12704;
    font-size: 24px;
    font-weight: 500;
    font-family: $SECONDARY_FONT;
}

.product-quantity{
    font-size: 16px;
    font-family: $SECONDARY_FONT;
}

.single-product-view-quantity{
    width: 50px;
    padding: 5px;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    color: $PRIMARY_COLOR;
    font-family: $SECONDARY_FONT;
}

.single-product-view-quantity:focus{
    outline: none;
}


.btn-add-cart{
    background-color: $SECONDARY_THEME_COLOR;
    border: none;
    color: $PRIMARY_HEADER_COLOR;
    padding: 10px;
    border-radius: 5px;
    font-size: 15px;
    align-items: center;
    width: 200px;
    text-transform: uppercase;
    font-family: $SECONDARY_FONT;
    font-weight: 500;
    letter-spacing: 1px;
    box-shadow: $SHADOW;
}

.delivery-info{
    display: flex;
    align-items: center;
    gap: 10px;
}

.delivery-info > span {
    color: #008000;
    font-family: $SECONDARY_FONT;
    font-size: 15px;
}

.tag-icon{
    color: #008000;
}

.add-cart-container{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.available-sizes{
    width: 2rem;
    height: 2rem;
    align-items: center;
    display: flex;
    justify-content: center;
    border: 1px solid #EBEBEB;
}

.available-sizes > span{
    font-size: 16px;
    color: $PRIMARY_COLOR;
    font-family: $SECONDARY_FONT;
}

.product-Description{
    width: 400px;
    font-family: $SECONDARY_FONT;
    font-size: 14px;
    text-align: justify;
}

@media screen and (max-width: 576px) {
    .product-screen-img{
        width: 100%;
    }

    .btn-add-cart{
        width: 100%;
    }

    .product-Description{
        width: 100%;
    }
    
}

/******************************************************************************************/
.product-modal{
    outline: none;
}

.modal-title{
    color: $PRIMARY_COLOR;
    font-size: 24px;
    font-weight: 700;
    font-family: $THIRD_FONT;
}


.product-modal-div{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    padding: 2rem;
}
.product-modal-img{
    width: 325px;
    padding: 1rem;
}

.product-modal-right{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.product-instock{
    width: max-content;
}

.product-modal-bottom{
    border-top: 1px solid lightgray;
    padding: 0.5rem 0;
    align-items: center;
    display: flex;
    justify-content: end;
}

.btn-view-details{
    background-color: $THIRD_THEME_COLOR;
    color: $PRIMARY_HEADER_COLOR;
    font-family: $SECONDARY_FONT;
    border-radius: 5px;
    font-size: 14px;
    padding: 10px 1rem;
    text-transform: uppercase;
    transition: $TRANSITION;
    box-shadow: $SHADOW;
}

@media screen and (max-width: 576px) {
    .product-modal-img{
        width: 90%;
    }
    
}
/*******************************************************************************************/
.recommend-container{
    display: flex;
    flex-direction: column;
    padding: 1rem;
    gap: 0.5rem;
    background-color: $PRIMARY_HEADER_COLOR;
    margin-top: 0.25rem;
    border-radius: 5px;
    width: 100%;
}

.recommend-title{
    font-family: $THIRD_FONT;
    font-size: 24px;
    text-transform: uppercase;
    font-weight: 600;
    color: $PRIMARY_COLOR;
}

.similar-products-cotainer{
    display: flex;
    flex-direction: row;
    overflow-x: scroll;
    width: 100%;
    align-items: center;
    gap: 1rem;
    padding: 5px;
    border-top: 1px solid $PRIMARY_BG_COLOR;
    padding-top: 1rem;
}

@media screen and (max-width: 576px) {
    .recommend-container{
        width: 100%;
    }
    
}





