@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&family=Poppins&family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed&display=swap');

$HEADER_TOP_BG: #FFFFFF;
$PRIMARY_COLOR: #131921;
$SECONDARY_COLOR: #222F3E;

$PRIMARY_FONT: "Poppins", sans-serif;
$SECONDARY_FONT: "Roboto", sans-serif;
$THIRD_FONT: "Roboto Condensed", sans-serif;

$PRIMARY_HEADER_COLOR: #FFFFFF;

$PRIMARY_THEME_COLOR: #FFA41C;
$SECONDARY_THEME_COLOR: #1995D3;
$PRIMARY_BG_COLOR: #F7F7F7;
$TRANSITION: all ease-in-out 0.3s;

$SHADOW: 0 1px 2px 0 rgba(0, 0, 0, 0.1), 0 2px 10px 0 rgba(0, 0, 0, 0.09);

.menu-item{
    text-align: left;
    cursor: pointer;
    padding: 0.5rem;
}

.menu-item:hover{
    background-color: #EAEDED;
}

.menu{
    position: fixed;
    overflow-y: scroll;
    top: 0;
    bottom: 0;
    left: 0;
    background-color: white;
    z-index: 1;
    display: flex;
    flex-direction: column;
    gap: 5px;
    min-width: 280px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.side-bar-header-links {
    text-decoration: none;
    font-size: 13px;
    font-family: $PRIMARY_FONT;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-decoration: none;
    color: #191919;
}

.menu::-webkit-scrollbar{
    display: none;
}

.side-menu-profile-container{
    display: flex;
    justify-content: space-between;
    background-color: $SECONDARY_COLOR;
    font-family: $SECONDARY_FONT;
    padding: 0.5rem;
    min-width: 280px;
    position: sticky;
    top: 0;
}

.user-info{
    font-size: 13px;
    letter-spacing: 1px;
    font-weight: 400;
    align-items: center;
    color: white;
    margin-left: 1rem;
    font-family: $SECONDARY_FONT;
}

.close-menu-icon{
    font-size: 1.5rem;
    cursor: pointer;
    color: white;
    font-weight: bold;
    transition: $TRANSITION;
}

.close-menu-icon:hover{
    transform: scale(1.25);
    transition: $TRANSITION;
}

.side-nav-links{
    font-size: 13px;
    font-family: $SECONDARY_FONT;
    color: $PRIMARY_COLOR;
    margin-left: 1rem;
}

.side-nav-links:hover{
    text-decoration: none;
    color: $PRIMARY_COLOR;
}

@media screen and (max-width: 576px) {
    .menu{
        width: 100%;
        position: static;
    }
    
}

/*******************************************************************************************/
.show-profile{
    width: 175px;
    display: none;
    position: absolute;
    z-index: 3000;
    background-color: white;
    right: 0;
    top: 35px;
    border-radius: 5px;
    margin-right: 0.75rem;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.19);
}

.show-profile.profile-active{
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
}

.show-profile > div {
    padding: 0.5rem;
    font-family: $SECONDARY_FONT;
    font-size: 14px;
    cursor: pointer;
    border-radius: 5px;
}

.show-profile > div:hover{
    background-color: #EBEBEB;
}

@media screen and (max-width: 576px) {

    .show-profile{
        width: 125px;
    }
    .show-profile > div {
        font-size: 12px;
    }
    
}
  


