.main-container{
    display: flex;
    justify-content: center;
    padding: 2rem 2rem;
}

.form-container{
    width: 30%;
    border: 2px solid lightgray;
    padding: 2rem;
    border-radius: 5px;
    margin-top: 0.5rem;
}

.form-labels{
    text-align: left;
    font-family: "Roboto";
    font-weight: bold;
    width: 100%;
}

.form-signin-btn{
    width: 100%;
    background-color: #F1C453;
    border: 1px solid #9C7E31;
    color: black;
    font-family: "Roboto";
    font-size: 16px;
}

@media screen and (max-width: 960px){

    .form-container{
     width: 65%;
    }
}
.main-admin-product-container{
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.main-admin-view-top-container{
    display: flex;
    flex-wrap: wrap;
    grid-gap: 1rem;
    gap: 1rem;
    margin: 1rem 1rem;
    justify-content: space-between;
}

.main-admin-product-top-container > div > span{
    font-size: 1.5rem;
    color: white;
    letter-spacing: 5px;
    font-family: 'Oswald', sans-serif;
}
.admin-product-table-container{
    overflow-y: scroll;
    height: 20rem;
    margin: 1rem 1rem;
    cursor: pointer;
}

.admin-product-table-container > div{
    margin: 1rem;
}

.admin-product-table-container > div >span{
    font-size: 1.5rem;
    font-family: 'Roboto';
    color: dimgrey;
}
.admin-product-view-table > thead > tr > th {
    font-family: 'Roboto';
    font-size: 14px;
    color: dimgray;
    letter-spacing: 1.1px;
}

.admin-product-img{
    width: 5rem;
}

