@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&family=Poppins&family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed&display=swap');

$HEADER_TOP_BG: #FFFFFF;
$PRIMARY_COLOR: #131921;
$SECONDARY_COLOR: #222F3E;

$PRIMARY_FONT: "Poppins", sans-serif;
$SECONDARY_FONT: "Roboto", sans-serif;
$THIRD_FONT: "Roboto Condensed", sans-serif;

$PRIMARY_HEADER_COLOR: #FFFFFF;

$PRIMARY_THEME_COLOR: #FFA41C;
$SECONDARY_THEME_COLOR: #1995D3;
$THIRD_THEME_COLOR: #1AB744;
$PRIMARY_BG_COLOR: #F7F7F7;
$TRANSITION: all ease-in-out 0.3s;

$SHADOW: 0 1px 2px 0 rgba(0, 0, 0, 0.1), 0 2px 10px 0 rgba(0, 0, 0, 0.09);

.main-shipping-container{
    display: flex;
    justify-content: center;
    background-color: #EAEDED;
    align-items: center;
    font-family: $SECONDARY_FONT;
    padding: 0.5rem;
    width: 100%;
}

.shipping-container{
    display: flex;
    align-items: center;
    width: 100%;
    border-radius: 5px;
    padding: 0.25rem;
    gap: 0.25rem;
    background-color: white;
    flex-direction: column;
}

.container-shipping{
    width: 500px;
    border: 1px solid #EBEBEB;
    padding: 1rem;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.shipping-form {
    width: 100%;
}

.shipping-form-title{
    font-size: 24px;
    font-family: $PRIMARY_FONT;
    color: $PRIMARY_COLOR;
}

.shipping-form > form {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.form-shipping-btn{
    font-size: 16px;
    color: white;
    font-weight: 550;
    text-transform: uppercase;
    border-radius: 5px;
    background-color: $SECONDARY_THEME_COLOR;
}

@media screen and (max-width: 576px) {
    .main-shipping-container{
        padding: 0.25rem;
    }
    .shipping-container{
        width: 100%;
        padding: 0.25rem;
    }

    .container-shipping{
        width: 100%;
    }

    .shipping-form-title{
        font-size: 16px;
    }

    .form-shipping-btn{
        font-size: 13px;
    }
}