@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&family=Poppins&family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed&display=swap');

$HEADER_TOP_BG: #FFFFFF;
$PRIMARY_COLOR: #131921;
$SECONDARY_COLOR: #222F3E;

$PRIMARY_FONT: "Poppins", sans-serif;
$SECONDARY_FONT: "Roboto", sans-serif;
$THIRD_FONT: "Roboto Condensed", sans-serif;

$PRIMARY_HEADER_COLOR: #FFFFFF;

$PRIMARY_THEME_COLOR: #FFA41C;
$SECONDARY_THEME_COLOR: #1995D3;
$THIRD_THEME_COLOR: #1AB744;
$PRIMARY_BG_COLOR: #F7F7F7;
$TRANSITION: all ease-in-out 0.3s;

$SHADOW: 0 1px 2px 0 rgba(0, 0, 0, 0.1), 0 2px 10px 0 rgba(0, 0, 0, 0.09);


.main-footer-container{
    display: flex;
    width: 100%;
    justify-content: center;
    flex-direction: column;
    background-color: $PRIMARY_COLOR;
}

.top-footer-container{
    padding: 1rem;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.top-footer-container > span{
    color: white;
    cursor: pointer;
    font-size: 14px;
}

.bottom-footer-container{
    height: auto;
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    gap: 4rem;
    padding: 1rem;
    justify-content: center;
}

.bottom-footer-column{
    display: flex;
    width: auto;
    justify-content: center;
}

.bottom-footer-column > ul > li{
    list-style-type: none;
    padding: 5px;
    text-align: left;
}

.bottom-footer-column > form > ul > li  {
    list-style-type: none;
    padding: 5px;
    text-align: left;
}

.footer-social-media-icons{
    color: #8C9196;
    font-size: 1.5rem;
    gap: 1rem;
    display: flex;
    cursor: pointer;
}


.footer-links{
    color: #8C9196;
    font-size: 15px;
    font-family: $SECONDARY_FONT;
    transition: $TRANSITION;
}

.footer-links:hover{
    text-decoration: none;
    transition: $TRANSITION;
    color: white;
}

.footer-column-title{
    font-size: 15px;
    color: white;
    font-family: $PRIMARY_FONT;
}

.newsletter-field {
    height: 2.5rem;
    width: 225px;
    padding: 10px;
    font-size: 14px;
    border-radius: 5px;
    border: none;
    font-family: $SECONDARY_FONT;
    border: 1px solid white;
}

.newsletter-btn{
    height: 2.5rem;
    width: 100%;
    margin-top: 0.5rem;
    border: none;
    font-size: 14px;
    background-color: $SECONDARY_THEME_COLOR;
    color: white;
    font-family: $SECONDARY_FONT;
    border-radius: 5px;
}

.footer-end-container{
    display: flex;
    flex-wrap: wrap;
    height: auto;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    font-size: 14px;
    width: 100%;
    padding: 1rem;
}

.footer-links-bottom{
    color: gray;
    text-decoration: none;
    font-family: $SECONDARY_FONT;
    font-size: 12px;
}

.footer-links-bottom:hover{
    text-decoration: none;
    color: gray;
}


@media screen and (max-width: 576px) {
    .footer-links{
        font-size: 13px;
    }

    .footer-end-container{
        justify-content: center;
    }
    
}

@media screen and (max-width: 700px){
    .bottom-footer-container{
        display: flex;
        justify-content: start;
        gap: 2rem;
    }
    .bottom-footer-column{
        display: flex;
        justify-content: center;
    }
}

