@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&family=Poppins&family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed&display=swap');

$HEADER_TOP_BG: #FFFFFF;
$PRIMARY_COLOR: #131921;
$SECONDARY_COLOR: #222F3E;

$PRIMARY_FONT: "Poppins", sans-serif;
$SECONDARY_FONT: "Roboto", sans-serif;
$THIRD_FONT: "Roboto Condensed", sans-serif;

$PRIMARY_HEADER_COLOR: #FFFFFF;

$PRIMARY_THEME_COLOR: #FFA41C;
$SECONDARY_THEME_COLOR: #1995D3;
$THIRD_THEME_COLOR: #1AB744;
$PRIMARY_BG_COLOR: #F7F7F7;
$TRANSITION: all ease-in-out 0.3s;

$SHADOW: 0 1px 2px 0 rgba(0, 0, 0, 0.1), 0 2px 10px 0 rgba(0, 0, 0, 0.09);


.main-cart-container{
    background-color: #EAEDED;
    padding: 0.5rem;
    font-family: $SECONDARY_FONT;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    gap: 1rem;
    font-size: 15px;
}

.main-cart{
    background-color: $PRIMARY_HEADER_COLOR;
    border: 1px solid #EBEBEB;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 2rem;
    padding: 1rem;
}

.cart-container{
    display: flex;
    border-radius: 5px;
    flex-direction: column;
    justify-content: center;
    padding: 1rem 1rem;
    align-items: center;
    background-color: $PRIMARY_HEADER_COLOR;
    border: 1px solid #EBEBEB;
}

.empty-cart-container{
    display: flex;
    flex-wrap: wrap;
    background-color: $PRIMARY_HEADER_COLOR;
    padding: 1rem;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    flex-direction: column;
}

.cart-status{
    color: $SECONDARY_COLOR;
    font-weight: 500;
    font-family: $SECONDARY_FONT;
    font-size: 20px;
}
.empty-cart-btn-container{
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
}

.btn-empty-cart{
    background-color: $PRIMARY_BG_COLOR;
    border: 1px solid #EBEBEB;
    border-radius: 5px;
    font-family: $SECONDARY_FONT;
}

.cart-list-container{
    display: flex;
    cursor: pointer;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    gap: 2rem;
    padding: 1rem;
    width: 450px;
    border-bottom: 1px solid #EBEBEB;
}

.cart-list-product-quantity{
    width: 3rem;
    border-radius: 5px;
    padding-left: 10px;
}

.cart-list-product-quantity:focus{
    outline: none;
}


.delete-item-icon{
    color: gray;
    font-size: 18px;
}

.delete-item-icon:hover{
    color: crimson;
}

.cart-total-container{
    background-color: $PRIMARY_HEADER_COLOR;
    padding: 1rem;
    height: min-content;
    display: flex;
    justify-content: center;
    gap: 1rem;
}

.cart-btn{
    padding: 0.75rem;
    border: none;
    text-transform: uppercase;
    font-size: 14px;
    
    border-radius: 5px;
    color: $PRIMARY_HEADER_COLOR;
    font-family: $SECONDARY_FONT;
    
}

.cart-btn:hover{
    opacity: 0.9;
}


@media screen and (max-width: 800px) {

    .empty-cart-img{
        width: 100%;
    }
    .cart-status{
        font-size: 14px;
    }
    .cart-list-container{
        width: 100%;
    }

    .main-cart{
        justify-content: center;
    }

    .cart-total-container{
        width: 100%;
    }

    .btn-empty-cart{
        width: 100%;
    }

    .cart-btn{
        padding: 0.75rem;
        font-size: 14px;
        width: 100%;
    }
    
}
