@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&family=Poppins&family=Roboto&display=swap');

$HEADER_TOP_BG: #FFFFFF;
$PRIMARY_COLOR: #232F3E;
$SECONDARY_COLOR: #37475A;

$PRIMARY_FONT: "Poppins", sans-serif;
$SECONDARY_FONT: "Roboto", sans-serif;

$PRIMARY_HEADER_COLOR: #FFFFFF;

$PRIMARY_THEME_COLOR: #1995D3;
$SECONDARY_THEME_COLOR: #1AB744;
$PRIMARY_BG_COLOR: #F5F6F9;
$TRANSITION: all ease-in-out 0.3s;

.main-order-history-container{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding: 0.5rem;
}

.order-history-title{
    background-color: #B0DAAD;
    padding: 5px;
    font-size: 16px;
    color: white;
}


.order-history-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    overflow-x:auto;
    font-family: $SECONDARY_FONT;
    letter-spacing: 0.5px;
    padding: 1rem;
    background-color: $PRIMARY_HEADER_COLOR;
}

.order-history-table > thead >  tr > th{
    font-size: 14px;
    color: $SECONDARY_COLOR;
    font-family: $SECONDARY_FONT;
}


.order-history-table > tbody > tr >  td{
    font-size: 14px;
    color: $SECONDARY_COLOR;
    font-family: $SECONDARY_FONT;
}

.btn-order-history{
    background-color: $PRIMARY_THEME_COLOR;
    font-size: 14px;
    border-radius: 5px;
    border: none;
    color: white;
    font-family: $PRIMARY_FONT;
}

@media screen and (max-width: 576px) {
    .order-history-title{
        font-size: 14px;
    }
    .order-history-table > thead >  tr > th{
        font-size: 12px;
    }
    .order-history-table > tbody > tr >  td{
        font-size: 12px;
    }
    .btn-order-history{
        font-size: 12px;
    }
    
}

