@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&family=Poppins&family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed&display=swap');

$HEADER_TOP_BG: #FFFFFF;
$PRIMARY_COLOR: #131921;
$SECONDARY_COLOR: #222F3E;

$PRIMARY_FONT: "Poppins", sans-serif;
$SECONDARY_FONT: "Roboto", sans-serif;
$THIRD_FONT: "Roboto Condensed", sans-serif;

$PRIMARY_HEADER_COLOR: #FFFFFF;

$PRIMARY_THEME_COLOR: #FFA41C;
$SECONDARY_THEME_COLOR: #1995D3;
$THIRD_THEME_COLOR: #1AB744;
$PRIMARY_BG_COLOR: #F7F7F7;
$TRANSITION: all ease-in-out 0.3s;

$SHADOW: 0 1px 2px 0 rgba(0, 0, 0, 0.1), 0 2px 10px 0 rgba(0, 0, 0, 0.09);


.main-login-container{
    display: flex;
    justify-content: center;
    width: 100%;
    background-color: $PRIMARY_BG_COLOR;
    align-items: center;
    font-family: $PRIMARY_FONT;
    padding: 1rem;

}

.login-container{
    background-color: white;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 2rem;
    border-radius: 5px;
    border: 1px solid #EBEBEB;
    width: 70%;
    gap: 1rem;
}

.login-left-container{
    width: 375px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.login-title{
    font-size: 16px;
    color: $PRIMARY_COLOR;
    font-family: $SECONDARY_FONT;
    text-transform: uppercase;
    font-weight: 550;
}

.login-right-container{
    display: flex;
    justify-content: start;
    background-color: white;
    flex-direction: column;
    padding: 1.5rem;
    border-radius: 5px;
    width: 375px;
    gap: 10px;
    border: 1px solid #EBEBEB;
}

.lbl-login{
    font-size: 14px;
    color: grey;
}

.txt-login{
    border-radius: 5px;
    padding: 10px 10px;
    padding-left: 1rem;
    border: none;
    width: 100%;
    font-size: 13px;
    color: $PRIMARY_COLOR;
    border: 1px solid lightgray;
}

.txt-login:focus{
    outline: none;
}

.btn-login{
    border: none;
    padding: 0.5em;
    font-size: 15px;
    width: 100%;
    color: white;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    background-color: $SECONDARY_THEME_COLOR;
    border-radius: 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.btn-login:hover{
    opacity: 0.9;
}

.login-links{
    color: $PRIMARY_COLOR;
    transition: $TRANSITION;
}

.login-links:hover{
    color: $SECONDARY_THEME_COLOR;
    text-decoration: none;
    transition: $TRANSITION;
}

.btn-google{
    height: 3rem;
    background-color: white;
    color: $PRIMARY_COLOR;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    width: 100%;
    gap: 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 5px;
}



@media screen and (max-width: 576px) {
    .main-login-container{
        padding: 0.25rem;
    }
    .login-container{
        width: 100%;
        flex-wrap: wrap;
        justify-content: center;
        padding: 0.75rem;
        padding-top: 1.5rem;
    }

    .login-right-container{
        padding: 1rem;
    }

    .btn-login{
        font-size: 14px;
    }
}